.form {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.label {
  display: flex;
  flex-direction: column;
  margin: 0.5rem auto;
  align-items: center;
  font-size: large;
  text-align: left;
  padding: 0.5rem 1rem 0.5rem 1rem;
}

.button {
  margin-top: 1rem;
  margin-bottom: 2rem;
  background-color: gray;
  border: none;
  border-radius: 5px;
  color: lightgray;
  flex-grow: 1;
  font-family: SFProText-Regular, Helvetica, Arial, sans-serif;
  font-size: 15px;
  height: 36px;
  line-height: 20px;
  margin-left: 8px;
  margin-right: 6px;
  min-width: 185px;
  padding: 0 16px 0 16px;
  color: white;
  cursor: pointer;
}

.button:hover {
  background-color:black;
  cursor: pointer;
}